import React, { Component } from 'react';
import {Grid, Cell, List, ListItem, ListItemContent} from 'react-mdl'
import Avatar from './avatar.png';

class Contact extends Component{
render(){
    return(
        <div className='contact-body'>
            <Grid className='contact-grid'>
                <Cell col={6}>
                    <h2>Furkat Kurganbaev</h2>
                    <img 
                        src = {Avatar}
                        alt="avatar" 
                    
                    style={{height: '200px', borderRadius: '100px'}}/>
                <p style={{width: '75%', margin: 'auto', paddingTop:'1em'}}> WHAT CAN YOUR NEW SITE BE?
As a developer I create sites that really help to do business. 
What advantages do you get by ordering a website from me? Firstly, 
information about your company's activities and product advantages will be easily displayed
 on the website.Secondly, thanks to an unlimited number of pages you can lay out a huge amount 
 of data (the history of the company's activities, vacancies, product catalog, descriptions, 
 price list, photo gallery, news, articles, useful tips, feedback form, guestbook, questions and
 answers, map of travel to the office, etc.).</p>
                </Cell>
                <Cell col={6}>
                    <h2>Contact Me</h2>
                    <hr/>
   <div className= "contact-list">                 
<List>
  <ListItem>
    <ListItemContent style ={{fontSize: '30px', fontFamily: 'Anton'}}>
        <i className= "fa fa-phone" aria-hidden="true"/>
        (998) 99 868 1973
        </ListItemContent>
  </ListItem>
  <ListItem>
    <ListItemContent style ={{fontSize: '30px', fontFamily: 'Anton'}}>
        <i className= "fa fa-envelope-o" aria-hidden="true"/>
        fkurganbaev@gmail.com
        </ListItemContent>
  </ListItem>
  <ListItem>
    <ListItemContent style ={{fontSize: '30px', fontFamily: 'Anton'}}>
        <i className= "fa fa-skype" aria-hidden="true"/>
        Furkat Kurganbaev
        </ListItemContent>
  </ListItem>
</List>
</div>

                    </Cell>
            </Grid>
        </div>
    )
}
}
export default Contact;