import React, { Component } from 'react';
import {Grid, Cell } from 'react-mdl';
import Avatar from './avatar.png';
class Landingpage extends Component{
render(){
    return(
        <div style={{width: '100%', margin: 'auto'}}>
<Grid className="Landing-grid">
<Cell col={12}>
<div style={{textAlign:'center'}}>
                        <img 
                        src = {Avatar}
                        alt="avatar" 
                    
                    style={{height: '300px', borderRadius: '200px'}}/>
                    </div>
{/* <img
src="https://tproger.ru/wp-content/uploads/2016/05/1463670650_malecostume.png"
alt="avatar"
className="avatar-img"
/> */}

<div className="banner-text">
    <h1>Full Stack Web Dewelopper</h1>
    <hr/>
    <p>HTML/CSS | Bootstrap | Javascript | React JS | Java | SQL | Database| Spring</p>
<div className="social-links">
    
    {/* LinkedIn */}
<a href="https://www.linkedin.com/feed/" rel="noopener noreferrer" target="_blank">
    <i className="fa fa-linkedin-square" aria-hidden="true" />
    </a>

    {/* Github */}
<a href="https://github.com/" rel="noopener noreferrer" target="_blank">
    <i className="fa fa-github-square" aria-hidden="true" />
    </a>

{/* FreeCodeCamp */}
<a href="https://www.freecodecamp.org/" rel="noopener noreferrer" target="_blank">
    <i className="fa fa-free-code-camp" aria-hidden="true" />
    </a>
    
{/* Youtube */}
<a href="https://www.youtube.com/" rel="noopener noreferrer" target="_blank">
    <i className="fa fa-youtube" aria-hidden="true" />
    </a>

    </div>
</div>
</Cell>
</Grid>
        </div>
    )
}
}
export default Landingpage;