import React, { Component } from 'react';
import {Grid, Cell } from 'react-mdl';
import Avatar from './avatar.png';

class About extends Component{
render(){
    return(
        
       <div style={{width: '100%', margin: 'auto'}}>
<Grid className="About-grid">
<Cell col={12}>
<div style={{textAlign:'center'}}>
<h1>Hi, I’m Furkat. Nice to meet you.</h1>
                        <img 
                        src = {Avatar}
                        alt="avatar" 
                    
                    style={{height: '200px', borderRadius: '100px'}}/>

<h3>I am a developer specialized in web development and web design. </h3>
<p className = "abouttext">My name is Furkat Kurganbaev. I am passionate about designing and developing
 user-friendly websites. I have all the skills to create a new high quality website or improve existing 
 websites for individuals and organisations, using the latest techniques and trends. I focus on modern 
 and responsive website solutions that are easy to update by the client. 
 From the beginning of my journey as a freelance web developer, designer, about 8 years ago, I worked 
 remotely for agencies and collaborated with talented people to create digital products for both 
 business and consumers. I am quietly confident, naturally curious, and constantly work to improve
  and replenish my knowledge.
 </p>                    
                    
</div>
        
</Cell>
</Grid>
</div>
)
}
}
export default About;